<template>
  <div>
    <el-form-item label="" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      ></el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>
    <el-form-item label="" prop="type">
      <el-select
        v-model="value.type"
        placeholder=""
        size="mini"
        @change="rangeChange"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
    </el-form-item>
    <el-form-item label="" prop="timestamp">
      <el-switch
        v-model="value.timestamp"
      />
    </el-form-item>
    <el-form-item v-if="value.type.indexOf('range')=='-1'" label="" prop="placeholder">
      <el-input v-model="value.placeholder" />
    </el-form-item>
    <el-form-item v-if="value.type.indexOf('range')!=-1" label="" prop="startPlaceholder">
      <el-input v-model="value.startPlaceholder" />
    </el-form-item>
    <el-form-item v-if="value.type.indexOf('range')!=-1" label="" prop="endPlaceholder">
      <el-input v-model="value.endPlaceholder" />
    </el-form-item>
    <el-form-item label="" prop="format">
      <el-input v-model="value.format" />
    </el-form-item>

    <el-form-item label="" prop="defaultValue">
      <el-date-picker
        v-model="value.defaultValue"
        range-separator="-"
        :type="value.type"
        style="width:100%"
        :format="format"
        :value-format="value.timestamp?'timestamp':value.format"
      />
    </el-form-item>

    <el-form-item label="Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      ></el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      ></el-checkbox>
      <el-checkbox
        v-model="value.readonly"
      ></el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      ></el-checkbox>
      <el-checkbox
        v-model="value.editable"
      ></el-checkbox>
      <el-checkbox
        v-model="value.clearable"
      ></el-checkbox>

    </el-form-item>
    <el-form-item label="">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        ></el-checkbox>
      </div>
    </el-form-item>
  </div>
</template>
<script>
export default {
  name: 'EDate',
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      options: [
        { label: 'year', value: 'year' },
        { label: 'month', value: 'month' },
        { label: 'date', value: 'date' },
        { label: 'dates', value: 'dates' },
        { label: 'week', value: 'week' },
        { label: 'datetime', value: 'datetime' },
        { label: 'datetimerange', value: 'datetimerange' },
        { label: 'daterange', value: 'daterange' },
        { label: 'monthrange', value: 'monthrange' }
      ]
    }
  },
  computed: {
    format: function() {
      if (this.value.type === 'week') {
        return 'yyyy  WW '
      }
      if (this.value.type === 'date') {
        return 'yyyy  MM  dd '
      }
      return 'yyyy-MM-dd'
    }
  },
  methods: {
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': ''
        })
      }
    },
    rangeChange() {
      this.value.defaultValue = ''
    }
  }

}
</script>
