<template>
  <div>
    <template v-if="!itemData.options.isRange">
      <el-time-select

        v-model="itemData.options.defaultValue"
        :name="itemData.model"
        :placeholder="itemData.options.placeholder"
        :disabled="itemData.options.disabled"
        :readonly="itemData.options.readonly"
        :editable="itemData.options.editable"
        :clearable="itemData.options.clearable"
        :class="itemData.options.customClass"
        :value-format="itemData.options.format"
        :arrow-control="itemData.options.arrowControl"
        :style="{'width':itemData.options.width}"
        @change="change"
      />
    </template>
    <template v-else>
      <el-time-picker
        v-model="itemData.options.defaultValue"
        is-range
        :name="itemData.model"
        :placeholder="itemData.options.placeholder"
        :disabled="itemData.options.disabled"
        :readonly="itemData.options.readonly"
        :editable="itemData.options.editable"
        :clearable="itemData.options.clearable"
        :class="itemData.options.customClass"
        :value-format="itemData.options.format"
        range-separator="-"
        :start-placeholder="itemData.options.startPlaceholder"
        :end-placeholder="itemData.options.endPlaceholder"
        :arrow-control="itemData.options.arrowControl"
        :style="{'width':itemData.options.width}"
        @change="change"
      />
    </template>
  </div>

</template>
<script>
export default {
  name: 'VTime', //
  props: {
    itemData: {
      type: Object,
      default: () => {
        return {
          'type': 'time', // 
          'icon': 'icon-time',
          'options': {
            'defaultValue': '', // 
            'readonly': false,
            'disabled': false, // 
            'editable': true, // 
            'clearable': true, // 
            'placeholder': '', // 
            'startPlaceholder': '',
            'endPlaceholder': '',
            'isRange': false,
            'arrowControl': true, // 
            'format': 'HH:mm:ss',
            'required': false, // 
            'width': '', // 
            'customClass': '', // class
            'labelWidth': 100, // 
            'isLabelWidth': false, // 
            'hidden': false, // 
            'dataBind': true,
            'remoteFunc': '',
            'remoteOption': ''
          },
          'name': '',
          'key': '',
          'model': '',
          'rules': []
        }
      }
    }
  },
  data() {
    return {
      data: '',
      name: ''
    }
  },
  created() {
    // this.data = this.itemData.options.defaultValue
    // this.name = this.itemData.model
    this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // form
  },
  methods: {
    change(val) {
      this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // form
    }
  }
}
</script>
