<template>
  <el-date-picker
    v-model="itemData.options.defaultValue"
    :name="itemData.model"
    :placeholder="itemData.options.placeholder"
    :disabled="itemData.options.disabled"
    :readonly="itemData.options.readonly"
    :editable="itemData.options.editable"
    :clearable="itemData.options.clearable"
    :class="itemData.options.customClass"
    :value-format="itemData.options.timestamp?'timestamp':itemData.options.format"
    :style="{'width':itemData.options.width}"
    :type="itemData.options.type"
    range-separator="-"
    :start-placeholder="itemData.options.startPlaceholder"
    :end-placeholder="itemData.options.endPlaceholder"
    :format="format"
    @change="change"
  />
</template>
<script>
export default {
  name: 'VDate', //
  props: {
    itemData: {
      type: Object,
      default: () => {
        return {
          'type': 'date', // 
          'icon': 'icon-time',
          'options': {
            'defaultValue': '', // 
            'readonly': false,
            'disabled': false, // 
            'editable': true, // 
            'clearable': true, // 
            'placeholder': '', // 
            'startPlaceholder': '',
            'endPlaceholder': '',
            'required': false, // 
            'width': '', // 
            'customClass': '', // class
            'labelWidth': 100, // 
            'isLabelWidth': false, // 
            'hidden': false, // 
            'dataBind': true,
            'remoteFunc': '',
            'remoteOption': '',
            'type': 'date',
            'format': 'yyyy-MM-dd',
            'timestamp': false
          },
          'name': '',
          'key': '',
          'model': '',
          'rules': []
        }
      }
    }
  },
  data() {
    return {
      data: '',
      name: ''
    }
  },
  computed: {
    format: function() {
      if (this.itemData.options.type === 'week') {
        return 'yyyy  WW '
      }
      if (this.itemData.options.type === 'date') {
        return 'yyyy  MM  dd '
      }
      return 'yyyy-MM-dd'
    }
  },
  created() {
    // this.data = this.itemData.options.defaultValue
    // this.name = this.itemData.model
    this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // form
  },
  methods: {
    change(val) {
      this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // form
    }
  }
}
</script>
