<template>
  <div>
    <el-form-item label="" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      ></el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>
    <el-form-item label="" prop="placeholder">
      <el-input v-model="value.placeholder" />
    </el-form-item>
    <el-form-item label="" prop="defaultValue">
      <el-input v-model="value.defaultValue" />
    </el-form-item>
    <el-form-item label="Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      ></el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      ></el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      ></el-checkbox>
    </el-form-item>
    <el-form-item label="">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        ></el-checkbox>
      </div>
      <div>
        <el-input
          v-model="value.pattern"
          placeholder=""
          size="mini"
          @input="rule"
        />
      </div>
    </el-form-item>
  </div>
</template>
<script>
export default {
  name: 'ETextarea',
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
    }
  },
  methods: {
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': ''
        })
      }

      if (this.value.pattern) {
        this.rules.push({
          'pattern': this.value.pattern,
          'message': ''
        })
      }
    }
  }

}
</script>
