<template>
  <div>
    <el-form-item label="" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      ></el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>
    <el-form-item label="" prop="placeholder">
      <el-input v-model="value.placeholder" />
    </el-form-item>
    <el-form-item label="" prop="defaultValue">
      <el-input v-model="value.defaultValue" />
    </el-form-item>
    <el-form-item label="Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      ></el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      ></el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      ></el-checkbox>
      <el-checkbox
        v-model="value.showPassword"
      ></el-checkbox>
    </el-form-item>
    <el-form-item label="">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        ></el-checkbox>
      </div>
      <el-select
        v-model="value.dataType"
        placeholder=""
        size="mini"
        @change="rule"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        />
      </el-select>
      <div>
        <el-input
          v-model="value.pattern"
          placeholder=""
          size="mini"
          @input="rule"
        />
      </div>
    </el-form-item>
  </div>
</template>
<script>
export default {
  name: 'EInput',
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
      options: [
        {
          label: '',
          value: 'string'
        },
        {
          label: '',
          value: 'number'
        },
        {
          label: '',
          value: 'boolean'
        },
        {
          label: '',
          value: 'integer'
        },
        {
          label: '',
          value: 'float'
        },
        {
          label: 'URL',
          value: 'url'
        },
        {
          label: '',
          value: 'email'
        },
        {
          label: '',
          value: 'hex'
        }
      ]
    }
  },
  methods: {
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': ''
        })
      }
      if (this.value.dataType) {
        this.rules.push({
          'type': this.value.dataType,
          'message': ''
        })
      }
      if (this.value.pattern) {
        this.rules.push({
          'pattern': this.value.pattern,
          'message': ''
        })
      }
    }
  }

}
</script>
