<template>
  <div>
    <el-form-item label="" prop="width">
      <el-input v-model="value.width" />
    </el-form-item>
    <el-form-item label="" prop="isLabelWidth">
      <el-checkbox
        v-model="value.isLabelWidth"
        style="margin-right:5px;"
      ></el-checkbox>
      <el-input-number
        v-model="value.labelWidth"
        :min="0"
        :max="200"
        :step="10"
        :disabled="!value.isLabelWidth"
      />
    </el-form-item>
    <el-form-item label="" prop="isRange">
      <el-switch
        v-model="value.isRange"
        @change="rangeChange"
      />
    </el-form-item>
    <el-form-item v-if="!value.isRange" label="" prop="placeholder">
      <el-input v-model="value.placeholder" />
    </el-form-item>
    <el-form-item v-if="value.isRange" label="" prop="startPlaceholder">
      <el-input v-model="value.startPlaceholder" />
    </el-form-item>
    <el-form-item v-if="value.isRange" label="" prop="endPlaceholder">
      <el-input v-model="value.endPlaceholder" />
    </el-form-item>
    <el-form-item label="" prop="format">
      <el-input v-model="value.format" />
    </el-form-item>

    <el-form-item v-if="!value.isRange" label="" prop="defaultValue">
      <el-time-select
        v-model="value.defaultValue"
        :picker-options="{
          start: '00:00',
          step: '00:15',
          end: '23:59'
        }"
      />
    </el-form-item>

    <el-form-item v-if="value.isRange" label="" prop="defaultValue">
      <el-time-picker
        v-model="value.defaultValue"
        is-range
        range-separator="-"
      />
    </el-form-item>

    <el-form-item label="Class" prop="customClass">
      <el-input v-model="value.customClass" />
    </el-form-item>
    <el-form-item label="">
      <el-checkbox
        v-model="value.dataBind"
        disabled
      ></el-checkbox>
      <el-checkbox
        v-model="value.hidden"
      ></el-checkbox>
      <el-checkbox
        v-model="value.readonly"
      ></el-checkbox>
      <el-checkbox
        v-model="value.disabled"
      ></el-checkbox>
      <el-checkbox
        v-model="value.editable"
      ></el-checkbox>
      <el-checkbox
        v-model="value.clearable"
      ></el-checkbox>
      <el-checkbox
        v-model="value.arrowControl"
      ></el-checkbox>
    </el-form-item>
    <el-form-item label="">
      <div>
        <el-checkbox
          v-model="value.required"
          @change="rule"
        ></el-checkbox>
      </div>
    </el-form-item>
  </div>
</template>
<script>
export default {
  name: 'ETime',
  props: {
    value: {
      type: Object,
      default: () => {
        return {}
      }
    },
    rules: {
      type: Array,
      default: () => {
        return []
      }
    }
  },
  data() {
    return {
    }
  },
  methods: {
    rule() {
      console.log(this.rules)
      this.rules.length = 0
      if (this.value.required) {
        this.rules.push({
          'required': true,
          'message': ''
        })
      }
    },
    rangeChange() {
      this.value.defaultValue = ''
    }
  }

}
</script>
