              'value': 'value',
              'label': 'label'
            },
            'customClass': '', // class
            'labelWidth': 100, // label
            'isLabelWidth': false, // label
            'hidden': false, // 
            'disabled': false // 
          },
          'name': '', // 
          'key': '1576726664245',
          'model': 'checkbox_1576726664245', // 
          'rules': [] // 
        }
      }
    }
  },
