<template>
  <el-radio-group
    v-model="itemData.options.defaultValue"
    :name="itemData.model"
    :disabled="itemData.options.disabled"
    @change="change"
  >
    <template v-if="itemData.options.button">
      <el-radio-button
        v-for="(item,index) in itemData.options.options"
        :key="index"
        :label="item.value"
        :style="style"
      >
        {{ item.label }}
      </el-radio-button>
    </template>
    <template v-else>
      <el-radio
        v-for="(item,index) in itemData.options.options"
        :key="index"
        :style="style"
        :label="item.value"
      >
        {{ item.label }}
      </el-radio>
    </template>
  </el-radio-group>
</template>
<script>
export default {
  name: 'VRadio',
  props: {
    itemData: {
      type: Object,
      default: () => {
        return {
          'type': 'radio', // 
          'options': {
            'button': false, // button
            'inline': false, // 
            'defaultValue': '1', // 
            'showLabel': false, // 
            'options': [ // 
              {
                'value': '1',
                'label': 'Option 1'
              },
              {
                'value': '2',
                'label': 'Option 2'
              },
              {
                'value': '3',
                'label': 'Option 3'
              }
            ],
            'required': false, // 
            'width': '', // 
            'props': {
              'value': 'value',
              'label': 'label'
            },
            'customClass': '', // class
            'labelWidth': 100, // label
            'isLabelWidth': false, // label
            'hidden': false, // 
            'disabled': false // 
          },
          'name': '', // 
          'key': '1576663508195',
          'model': 'radio_1576663508195', // 
          'rules': [] // 
        }
      }
    }
  },
  data() {
    return {
      data: '',
      name: ''
    }
  }, // 
  computed: {
    style() {
      return {
        display: this.itemData.options.inline || this.itemData.options.button ? 'inline-block' : 'block',
        margin: this.itemData.options.inline && !this.itemData.options.button ? '4px 30px 4px 0' : '5px 0'
      }
    }
  },
  created() {
    // this.data = this.itemData.options.defaultValue
    // this.name = this.itemData.model
    this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // form
  },
  methods: {
    change(val) {
      this.$emit('change', this.itemData.options.defaultValue, this.itemData.model) // form
    }
  }
}
</script>
